.testimonials {
  text-align: center;
  background-color: #f3f4f6;
  position: relative;
  overflow: hidden;
  margin: auto;
  user-select: none;
}
.testimonials__container {
  width: 100%;
  padding: 3rem 0rem 7rem;
}
.testimonials__content {
  margin: auto;
  overflow: hidden;
  position: relative;
}
.testimonials__content > div {
  padding: 0 0 1.8rem 0;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
}
.testimonials__content > div.inactive {
  opacity: 1;
}
.testimonials__content > div.inactive .img img {
  animation: testimonial-hide 0.5s ease-in-out forwards;
}
.testimonials__content > div.inactive h2 {
  animation: testimonial-content-out 0.5s ease-in-out forwards;
}
.testimonials__content > div.inactive p {
  animation: testimonial-content-out 0.5s ease-in-out forwards;
}
.testimonials__content > div.active {
  opacity: 1;
  position: relative;
}
.testimonials__content > div.active .img img {
  animation: testimonial-show 0.5s ease-in-out forwards;
}
.testimonials__content > div.active h2 {
  animation: testimonial-content-in 0.5s ease-in-out forwards;
}
.testimonials__content > div.active p {
  animation: testimonial-content-in 0.5s ease-in-out forwards;
}
.testimonials__content div .img img {
  display: block;
  width: 15rem;
  height: 15rem;
  margin: auto;
  border-radius: 50%;
}
@media (max-width: 39.9375em) {
  .testimonials__content div .img img {
    width: 10rem;
    height: 10rem;
  }
}
.testimonials__content div h2 {
  color: #2d314d;
  font-size: 2.4rem;
  margin: 1.5rem 0;
  font-weight: 700;
}
.testimonials__content div p {
  font-size: 1.8rem;
  color: black;
  margin: auto;
  width: 50%;
  word-spacing: 0.2rem;
}
.testimonials .arrow {
  display: block;
  position: absolute;
  cursor: pointer;
  top: 50%;
  transition: all 0.3s ease-in-out;
  padding: 1rem;
  z-index: 22222222;
}
.testimonials .arrow svg {
  width: 3rem;
  height: 3rem;
}
.testimonials .arrow:before {
  cursor: pointer;
}
.testimonials .arrow:hover {
  transform: scale(1.5);
  transition: all 0.2s ease-in-out;
}
.testimonials .arrow.right {
  right: 5rem;
}
@media (min-width: 64em) {
  .testimonials .arrow.right {
    right: 15rem;
  }
}
@media (max-width: 39.9375em) {
  .testimonials .arrow.right {
    right: 1rem;
  }
}
.testimonials .arrow.left {
  left: 5rem;
}
@media (min-width: 64em) {
  .testimonials .arrow.left {
    left: 15rem;
  }
}
@media (max-width: 39.9375em) {
  .testimonials .arrow.left {
    left: 1rem;
  }
}
.testimonials .dots {
  text-align: center;
  position: absolute;
  width: 100%;
  bottom: 4rem;
  left: 0;
  display: block;
  z-index: 3333;
  height: 1.2rem;
  margin: auto;
}
.testimonials .dots .dot {
  list-style-type: none;
  display: inline-block;
  width: 1.2rem;
  height: 1.2rem;
  border-radius: 50%;
  border: 1px solid #2d314d;
  margin: 0 1rem;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  position: relative;
}
.testimonials .dots .dot.active, .testimonials .dots .dot:hover {
  background: #2d314d;
  border-color: #2d314d;
}
.testimonials .dots .dot.active {
  animation: testimonials-scale 0.5s ease-in-out forwards;
}

@keyframes testimonial-scale {
  0% {
    box-shadow: 0px 0px 0px 0px #eee;
  }
  35% {
    box-shadow: 0px 0px 10px 5px #eee;
  }
  70% {
    box-shadow: 0px 0px 10px 5px #ea830e;
  }
  100% {
    box-shadow: 0px 0px 0px 0px #ea830e;
  }
}
@keyframes testimonial-content-in {
  from {
    opacity: 0;
    transform: translateY(100%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes testimonial-content-out {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(-100%);
  }
}
@keyframes testimonial-show {
  from {
    opacity: 0;
    transform: scale(0);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes testimonial-hide {
  from {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: 0;
    transform: scale(0);
  }
}
